@import '../../../sass/abstracts/mixins';
.grid {
    width: 100%;
    display: block;
    padding: 0;
}
  
.grid.wide {
    max-width: 1200px;
    margin: 0 auto;    
    @include reponsive(sm-desktop) {
        max-width: 984px;
    }
    @include reponsive(tablet-land) {
        max-width: 850px;
    }
    @include reponsive(tablet) {
        max-width: 680px;
    }
    @include reponsive(phone) {
        width: 100%;
    }
    
}
  
.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -12px;
    margin-right: -12px;
    @include reponsive(phone) {
        margin-left: 0;
        margin-right: 0;
    }
    &-reverse {
        flex-wrap: wrap-reverse;
    }
}
  
.row.no-gutters {
    margin-left: 0;
    margin-right: 0;
    align-items: center;
}
  
.col {
    padding-left: 12px;
    padding-right: 12px;
}
  
.row.no-gutters .col {
    padding-left: 0;
    padding-right: 0;
}
.col-0 {
    display: none;
}
.col-1 {
    flex: 0 0 8.33333%;
    max-width: 8.33333%;
}

.col-2 {
    flex: 0 0 16.66667%;
    max-width: 16.66667%;
}

.col-3 {
    flex: 0 0 25%;
    max-width: 25%;
}

.col-4 {
    flex: 0 0 33.33333%;
    max-width: 33.33333%;
}

.col-5 {
    flex: 0 0 41.66667%;
    max-width: 41.66667%;
    
}

.col-6 {
    flex: 0 0 50%;
    max-width: 50%;
}

.col-7 {
    flex: 0 0 58.33333%;
    max-width: 58.33333%;
}

.col-8 {
    flex: 0 0 66.66667%;
    max-width: 66.66667%;
}

.col-9 {
    flex: 0 0 75%;
    max-width: 75%;
}

.col-10 {
    flex: 0 0 83.33333%;
    max-width: 83.33333%;
}

.col-11 {
    flex: 0 0 91.66667%;
    max-width: 91.66667%;
}

.col-12 {
    flex: 0 0 100%;
    max-width: 100%;
}

.col-o-1 {
    margin-left: 8.33333%;
}

.col-o-2 {
    margin-left: 16.66667%;
}

.col-o-3 {
    margin-left: 25%;
}

.col-o-4 {
    margin-left: 33.33333%;
}

.col-o-5 {
    margin-left: 41.66667%;
}

.col-o-6 {
    margin-left: 50%;
}

.col-o-7 {
    margin-left: 58.33333%;
}

.col-o-8 {
    margin-left: 66.66667%;
}

.col-o-9 {
    margin-left: 75%;
}

.col-o-10 {
    margin-left: 83.33333%;
}

.col-o-11 {
    margin-left: 91.66667%;
}

.hidden {
    display: block;
    @include reponsive(tablet) {
        display: none;
    }
}
